// style.scss
$col1: 500px;
$col2: auto;
$gap: 20px;
$header-bg: #b3a9a5;
$main-bg: #ecf0f1;
$text-color: white;
$primary-color: #3b3550;
*{
    margin:0;
    border: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-color: $primary-color;
    height: 100%;
}
.container-main {
    display:flex;
    height: 100vh;
}
.container-header {
    background-color: $header-bg;
    color: white;
    display: flex;
    flex-direction: column;
}
.container-header-section{
    background-color: $main-bg;
    color: white;
    display: flex;
    flex-direction: column;
}

.container-header nav ul {
    list-style-type: none;
    padding: 0;
}